import app from '../../admin/app';
import DashboardWidget from './DashboardWidget';
import listItems from '../../common/helpers/listItems';
import ItemList from '../../common/utils/ItemList';
import Dropdown from '../../common/components/Dropdown';
import Button from '../../common/components/Button';
import LoadingModal from './LoadingModal';
import LinkButton from '../../common/components/LinkButton';
import saveSettings from '../utils/saveSettings.js';
export default class StatusWidget extends DashboardWidget {
  className() {
    return 'StatusWidget';
  }
  content() {
    return m("ul", null, listItems(this.items().toArray()));
  }
  items() {
    const items = new ItemList();
    items.add('tools', m(Dropdown, {
      label: app.translator.trans('core.admin.dashboard.tools_button'),
      icon: "fas fa-cog",
      buttonClassName: "Button",
      menuClassName: "Dropdown-menu--right"
    }, this.toolsItems().toArray()));
    items.add('version-flarum', [m("strong", null, "Flarum"), m("br", null), app.forum.attribute('version')], 100);
    items.add('version-php', [m("strong", null, "PHP"), m("br", null), app.data.phpVersion], 90);
    items.add('version-db', [m("strong", null, app.data.dbDriver), m("br", null), app.data.dbVersion], 80);
    if (app.data.schedulerStatus) {
      items.add('schedule-status', [m("span", null, m("strong", null, app.translator.trans('core.admin.dashboard.status.headers.scheduler-status')), ' ', m(LinkButton, {
        href: "https://docs.flarum.org/scheduler",
        external: true,
        target: "_blank",
        icon: "fas fa-info-circle"
      })), m("br", null), app.data.schedulerStatus], 70);
    }
    items.add('queue-driver', [m("strong", null, app.translator.trans('core.admin.dashboard.status.headers.queue-driver')), m("br", null), app.data.queueDriver], 60);
    items.add('session-driver', [m("strong", null, app.translator.trans('core.admin.dashboard.status.headers.session-driver')), m("br", null), app.data.sessionDriver], 50);
    return items;
  }
  toolsItems() {
    const items = new ItemList();
    items.add('clearCache', m(Button, {
      onclick: this.handleClearCache.bind(this)
    }, app.translator.trans('core.admin.dashboard.clear_cache_button')));
    items.add('toggleAdvancedPage', m(Button, {
      onclick: () => {
        saveSettings({
          show_advanced_settings: !app.data.settings.show_advanced_settings
        });
        if (app.data.settings.show_advanced_settings) {
          m.route.set(app.route('advanced'));
        }
      }
    }, app.translator.trans('core.admin.dashboard.toggle_advanced_page_button')));
    return items;
  }
  handleClearCache(e) {
    app.modal.show(LoadingModal);
    app.request({
      method: 'DELETE',
      url: app.forum.attribute('apiUrl') + '/cache'
    }).then(() => window.location.reload()).catch(e => {
      if (e.status === 409) {
        app.alerts.clear();
        app.alerts.show({
          type: 'error'
        }, app.translator.trans('core.admin.dashboard.io_error_message'));
      }
      app.modal.close();
    });
  }
}
flarum.reg.add('core', 'admin/components/StatusWidget', StatusWidget);