import Component from '../../common/Component';
import classList from '../../common/utils/classList';
export default class FormSectionGroup extends Component {
  view(vnode) {
    const {
      className,
      ...attrs
    } = this.attrs;
    return m("div", Object.assign({
      className: classList('FormSectionGroup', className)
    }, attrs), vnode.children);
  }
}
flarum.reg.add('core', 'admin/components/FormSectionGroup', FormSectionGroup);